import React, { useState } from "react";
import { useInView } from 'react-intersection-observer';
import CardNoticies from "../components/CardNoticies";

interface NoticesCardProps {
  title: string;
  description: string;
  text: string;
  onClick: () => void;
}

const NoticesCard: React.FC<NoticesCardProps> = ({ title, description, onClick }) => (
  <div className="bg-white rounded-xl p-5 m-4 w-full sm:m-5 min-h-[350px] flex flex-col shadow-lg lg:w-1/2 md:w-2/3 sm:w-3/4 xl:w-1/3">
    <p className="font-semibold text-[#5f6952] sm:text-xl text-justify uppercase">{title}</p>
    <p className="font-medium text-[#5f6952] sm:text-xl text-justify py-5">{description}</p>
    <button className="bg-[#5f6952] hover:bg-[#909E7D] w-full p-2 rounded-lg text-white font-semibold sm:text-lg tracking-wider mt-auto" onClick={onClick}>
      SAIBA MAIS
    </button>
  </div>
);

export default function Notices() {
  const [selectedNotice, setSelectedNotice] = useState<{ title: string; text: string } | null>(null);

  const noticeLine1 = [
    {
      title: 'Como Reduzir o Consumo de Energia em Pequenas Empresas.',
      description: 'Descubra estratgias prticas e eficazes para reduzir o consumo de energia e economizar custos operacionais em sua pequena empresa.',
      text: 'Para reduzir o consumo de energia e economizar custos, realize uma auditoria energtica e adote tecnologias eficientes, como iluminao LED e sistemas de aquecimento e resfriamento mais econmicos. Utilize sensores de movimento e controle de temperatura para minimizar desperdcios. Investir em energia renovvel tambm pode gerar economias e contribuir para a sustentabilidade.'
    },
    {
      title: 'A Importncia da Gesto Adequada de Resduos',
      description: 'Aprenda sobre as melhores prticas para a gesto de resduos e como uma abordagem eficaz pode melhorar a sustentabilidade da sua empresa e cumprir as regulamentaes ambientais.',
      text: 'Uma gesto eficaz de resduos envolve a implementao de programas de reciclagem, educao dos funcionrios e parcerias com empresas de reciclagem e compostagem. Reduzir, reutilizar e optar por fornecedores sustentveis ajuda a cumprir regulamentaes e pode reduzir custos, melhorando a imagem da empresa.'
    }
  ];
  const noticeLine2 = [
    {
      title: 'Tendncias em Sustentabilidade Corporativa para 2024.',
      description: 'Explore as principais tendncias e inovaes em sustentabilidade corporativa que esto moldando o futuro dos negcios e como sua empresa pode se adaptar e se beneficiar delas.',
      text: 'As principais tendncias em sustentabilidade corporativa incluem a adoo de tecnologias verdes, economia circular e metas de neutralidade de carbono. Inovaes como sistemas de monitoramento ambiental e inteligncia artificial so cada vez mais comuns. Adaptar-se a essas tendncias pode melhorar a imagem da empresa e criar oportunidades de mercado.'
    },
    {
      title: 'Impacto da Automao na Sustentabilidade Empresarial.',
      description: 'Explore como a automao pode contribuir para prticas empresariais mais sustentveis e eficientes.',
      text: 'A automao pode tornar as prticas empresariais mais sustentveis e eficientes, reduzindo desperdcios e otimizando processos. Sistemas automatizados para controle de energia e monitoramento de processos melhoram a eficincia e preciso, reduzindo a necessidade de intervenes humanas e alinhando operaes com prticas sustentveis.'
    }
  ];

  const handleCardClick = (title: string, text: string) => { setSelectedNotice({ title, text }) };
  const handleCloseCard = () => { setSelectedNotice(null); };
  const { ref: noticiesRef, inView: noticiesInView } = useInView({ triggerOnce: false, threshold: 0.2 });

  return (
    <div id="noticies" ref={noticiesRef} className={`flex justify-center`}>
      <div className="sm:w-4/5 my-14 sm:rounded-xl bg-[#909E7D] items-center justify-center">
        <h1 className="text-white font-bold text-4xl text-center mt-14 lg:text-5xl">NOTCIAS</h1>
        <div className="flex flex-col justify-center items-center py-8">
          <p className="font-medium text-md uppercase text-justify px-5 w-full sm:font-semibold sm:w-4/5 sm:text-xl text-[#4a5140]">
            Mantenha-se atualizado com as ltimas tendncias, dicas e notcias sobre sustentabilidade e prticas ambientais.
            Explore nossos artigos para obter insights valiosos e aprender como sua empresa pode fazer a diferena.
          </p>
          <div className="flex flex-wrap justify-center">
            {noticeLine1.map((notice, index) => (
              <NoticesCard
                key={index}
                title={notice.title}
                description={notice.description}
                text={notice.text}
                onClick={() => handleCardClick(notice.title, notice.text)} // Passa os dados do card clicado
              />
            ))}
          </div>
          <div className="flex flex-wrap justify-center">
            {noticeLine2.map((notice, index) => (
              <NoticesCard
                key={index}
                title={notice.title}
                description={notice.description}
                text={notice.text}
                onClick={() => handleCardClick(notice.title, notice.text)} // Passa os dados do card clicado
              />
            ))}
          </div>
        </div>
      </div>
      {selectedNotice && (
        <div className={`fixed inset-0 bg-gray-800 bg-opacity-50 flex justify-center items-center`}>
          <div className={`bg-white rounded-xl p-8 w-2/3 mx-4 relative ${noticiesInView ? 'animate-jump-in' : 'animate-none'}`}>
            <CardNoticies
              title={selectedNotice.title}
              text={selectedNotice.text}
              onClose={handleCloseCard} // Fecha o card
            />
          </div>
        </div>
      )}
    </div>
  );
}
